/* eslint-disable */
import React, {useState} from 'react';
import { Navbar, Container,Nav,NavDropdown,Button} from 'react-bootstrap';
import './App.css';
import dataValue from './data.js'; //내가 따로 빼놓은 데이터 받는방법 
import Detail from './Detail.js';

import {Link, Route, Switch} from 'react-router-dom';
import axios from 'axios';


function App() {

  let [data,dataChange] = useState(dataValue); //적어놓은 데이터 받아서 쓰는방법!
  let [product,productChange] = useState([10,11,12]);

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
      <Container>
        <Navbar.Brand href="#/">React-Bootstrap</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/">Home </Nav.Link>
            <Nav.Link as={Link} to="Detail/0"> Detail </Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Container>
      </Navbar>


      <Route exact path="/">
      <div className="panal">
        <h1 style={{paddingTop:'12%',color:'white'}}>20%할인</h1>
        <p style={{color:'white',margin:'1%'}}>
          대충 할인해주겠다는 내용임..하마 쿠쿠쿠쿠쿠다스<br/>
          흐음 흐 으으으으음~ 쿠쿠다스 과자먹고싶다... 
        </p>
        <Button variant="primary" style={{margin:'1%'}}>Primary</Button>
      </div>

      {/*  상품 뿌려주는곳.. */}
      <div className="container" style={{padding:'1%'}}>
        <div className="row">
        {data.map(function(e,index){
          return <ProductShoes Pdata={e} Pindex={index} key={index}></ProductShoes>
        })}
        </div>

        
        

        <Button className="btn btn-primary" onClick={()=>{
          axios.get('https://codingapple1.github.io/shop/data2.json')
          .then((result)=>{
            console.log(result.data);
            dataChange([...data, ...result.data]);

          })
          .catch(()=>{

          });
        }}>더보기</Button>



      </div>
      </Route>
        

  <Switch>
    <Route exact path="/detail/:id">
      <Detail shoes ={data} product={product} productChange={productChange}/>
    </Route>

    <Route exact path="/:id">
          <div>아무거나 적었을떄 이거보여주세요!</div>
    </Route>
  </Switch>
  

    </div>
  );
}



function ProductShoes(props){
  return <div className="col-md-4">
  <img src={"https://codingapple1.github.io/shop/shoes"+ (props.Pindex+1) + ".jpg"} width="100%"/>
  <h4>{props.Pdata.title}</h4>
  <p>{props.Pdata.content}</p>
  <p>{props.Pdata.price}</p>
  </div>
}

export default App;
